<template>
  <a>
    <v-btn
      fab
      class="facebook"
      color="info"
      @click=push(path)
    >
      <v-icon large>
        mdi-facebook
      </v-icon>
    </v-btn>
  </a>
</template>
<script>
  export default {
    props: {
      large: {
        type: Boolean,
        default: false
      }
    },
    data: () => ({
      path: 'https://www.facebook.com/pomocdrogowaopoczno/'
    }),
    methods: {
      push(path) {
        window.open(path, '_blank')
      }
    }
  }
</script>
